.Vender{
    background-image: url('../../img/imgTwo.png');
    background-size: 100%;
    background-position: center;
    height: 600px;
    display: flex;
    flex-direction: column;
    margin-bottom: 100px;
    margin-top: 100px;
}
.contVender{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: auto;
}
.titleVender{
    color: #0a1958;
    text-align: center;
    text-shadow: 10px 10px 100px #FFF;
    font-family: 'Paytone One', sans-serif;
    font-size: 60px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    width: 800px;
    margin: auto;
}
.textVender{
    color: #141930;
    text-align: center;
    text-shadow: 10px 10px 100px #FFF;
    font-family: Patua One;
    font-size: 36px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    width: 800px;
    margin: auto;
}
.containerButtonVender{
    display: flex;
    flex-direction: row;
    gap: 20px;
    margin-top: 20px;
    justify-content: center;
}
.buttonVender{
    height: 50px;
    width: 190px;
    border: none;
    border-radius: 10px;
    background: #0a1958;
    color: white;
    text-align: center;
    font-family: Patua One;
    font-size: 20px;
    font-style: normal;
    font-weight: 100;
    line-height: normal;
}

/*PHONE*/
@media (min-width: 300px) and (max-width: 500px) {
.Vender{
    background-image: url('../../img/imgTwo.png');
    background-size: 100% 100%;
    background-position: center;
    background-repeat: no-repeat;
    height: 200px;
    display: flex;
    flex-direction: column;
    margin-bottom: 50px;
}
.contVender{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: auto;
}
.titleVender{
    color: #233AA1;
    text-align: center;
    text-shadow: 10px 10px 100px #FFF;
    font-family: Patua One;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    width: 200px;
    margin: auto;
}
.textVender{
    color: #1C2138;
    text-align: center;
    text-shadow: 10px 10px 100px #FFF;
    font-family: Patua One;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    width: 200px;
    margin: auto;
}
.containerButtonVender{
    display: flex;
    flex-direction: row;
    gap: 20px;
    margin-top: 10px;
    justify-content: center;
}
.buttonVender{
    height: 20px;
    width: 70px;
    border: none;
    border-radius: 5px;
    background: #233AA1;
    color: white;
    text-align: center;
    font-family: Patua One;
    font-size: 11px;
    font-style: normal;
    font-weight: 100;
    line-height: normal;
}
}