.contCarrQuienSoy{
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.textCarrQuienSoy{
    color: #233AA1;
    text-align: center;
    font-family: Patua One;
    font-size: 60px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin-top: 70px;
}
.buttonQuienSoy{
    height: 60px;
    width: 260px;
    border: none;
    border-radius: 10px;
    background-color: #071038;
    color: white;
    text-align: center;
    font-family: Patua One;
    font-size: 20px;
    font-style: normal;
    font-weight: 100;
    line-height: normal;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 20px auto 100px auto;
    transition: background-color 0.5s ease-out;
}
.buttonQuienSoy:hover{
    background-color: #172d8f;
}
/*PHONE*/
@media (min-width: 300px) and (max-width: 500px) {
    .contCarrQuienSoy{
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    .textCarrQuienSoy{
        color: #233AA1;
        text-align: center;
        font-family: Patua One;
        font-size: 18px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
        margin-top: 20px;
    }
    .buttonQuienSoy{
        height: 30px;
        width: 130px;
        border: none;
        border-radius: 5px;
        background: #233AA1;
        color: white;
        text-align: center;
        font-family: Patua One;
        font-size: 11px;
        font-style: normal;
        font-weight: 100;
        line-height: normal;
        margin: 0px auto 10px auto;
    }
}