.CardCarr{
    width: 450px;
    border: none;
}
.containerImgCarr{
    height: 250px;
}
.imgCardCarr{
    width: 450px;
    height: 250px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}
.textCardCarr{
    display: flex;
    flex-direction: column;
    gap: 6px;
    border: 1px solid #233AA1;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    width: 448px;
}
.titleCardCarr{
    color: #1C2138;
    font-family: Patua One;
    font-size: 22px;
    padding: 10px 20px;
    text-align: center;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin: 0;
}
.contItemsCarr{
    display: flex;
    flex-direction: row;
    padding: 5px 20px;
    align-items: center;
    margin: auto;
    gap: 10px;
}
.itemsCardCarr{
    display: flex;
    flex-direction: row;
    gap: 5px;
}
.imgCaracCardCarr{
    width: 18px;
    height: 18px;
}
.caracCardCarr{
    color: #1C2138;
    font-family: Patua One;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin: 0;
}
.containerTwoCarr{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    height: 50px;
}
.priceCardCarr{
    color: #1C2138;
    font-family: Patua One;
    font-size: 30px;
    padding: 5px 25px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin: 0;
}
.contButtonCardCarr{
    margin-right: 20px;
    display: flex;
    flex-direction: row;
    z-index: 9999;
    align-items: start;
}
.textButtonCardCarr{
    color: #ffffff;
    font-family: Patua One;
    font-size: 11px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin: 0;
}
.buttonCardCarr{
    width: 40px;
    height: 40px;
    background: #1a203b;
    border: none;
    border-radius: 10px;
    margin: 0 5px;
}
.extenButtonCarr{
    width: 80px !important;
}
.iconCardCarr{
    width: 19px;
    height: 19px;
}

/*PHONE*/
@media (min-width: 300px) and (max-width: 500px) {
.CardCarr{
    width: 220px;
    height: 140px;
    border: none;
    margin-bottom: 120px;
}
.containerImgCarr{
    height: auto;
}
.imgCardCarr{
    width: 220px;
    height: 140px;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}
.space{
    height: 0;
}
.textCardCarr{
    display: flex;
    flex-direction: column;
    gap: 6px;
    padding-left: 8px;
    border: 1px solid #233AA1;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    width: 210px;
}
.titleCardCarr{
    color: #1C2138;
    font-family: Patua One;
    font-size: 15px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin: 0;
}
.contItemsCarr{
    display: flex;
    flex-direction: row;
    width: 100%;
    gap: 5px;
    margin: 3px 0px;
}
.itemsCardCarr{
    display: flex;
    flex-direction: row;
    gap: 2px;
}
.imgCaracCardCarr{
    width: 10px;
    height: 10px;
}
.caracCardCarr{
    color: #000;
    font-family: Pavanam;
    font-size: 9px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin: 0;
}
.descCardCarr{
    color: #000;
    font-family: Pavanam;
    font-size: 10px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin: 0;
}
.containerTwoCarr{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    height: 30px;
}
.priceCardCarr{
    color: #1C2138;
    font-family: Patua One;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin: 0;
}
.contButtonCardCarr{
    margin-right: 5px;
    display: flex;
    flex-direction: row;
    z-index: 9999;
    align-items: start;
}
.textButtonCardCarr{
    color: #ffffff;
    font-family: Patua One;
    font-size: 9px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin: 0;
    text-align: center;
}
.buttonCardCarr{
    width: 20px;
    height: 20px;
    background: #233AA1;
    border: none;
    border-radius: 5px;
    margin: 0 3px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.extenButtonCarr{
    width: 55px !important;
}
.iconCardCarr{
    width: 10px;
    height: 10px;
}
}