.Footer{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    height: 350px;
    gap: 100px;
    position: relative;
    margin-top: 200px;
}
.Footer::before {
    content: "";
    position: absolute;
    top: 0;
    width: 1400px;
    height: 3px;
    background-color: #0a1958;
}
.contfotList{
    height: 220px;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.fotList{
    list-style: none;
    text-decoration: none;
    padding: 0;
}
.fotSecc{
    color: #0a1547;
    font-family: Patua One;
    font-size: 27px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    text-align: center;
    margin: auto;
}
.fotSeccTitle{
    color: #0a1958;
    font-family: 'Paytone One', sans-serif;
    font-size: 36px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    text-align: center;
    margin: 0;
}
.containerInfo{
    display: flex;
    flex-direction: column;
    height: 220px;
}
.titleFotEli{
    color: #0a1958;
    text-align: center;
    font-family: 'Paytone One', sans-serif;
    font-size: 40px;
    font-style: normal;
    margin: 0;
}
.textFotEli{
    color: #0a1958;
    text-align: center;
    font-family: Patua One;
    font-size: 23px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin: 0;
}
.contButtonFooter{
    display: flex;
    flex-direction: row;
    gap: 10px;
    justify-content: center;
    margin-top: 10px;
}
.buttonFooter{
    height: 45px;
    width: 45px;
    background: #0a1958;
    border: none;
    border-radius: 10px;
}
.iconFoot{
    width: 30px;
    height: 30px;
    color: white;
}

/*PHONE*/
@media (min-width: 300px) and (max-width: 500px) {
.backFooter{
    position: absolute;
    top: 100;
    left: 0;
    width: 100%;
    height: 60%;
    background: linear-gradient(to top, #233AA1, #ffffff);
    z-index: -1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.Footer{
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
    align-items: center;
    margin-top: 0px;
    height: auto;
    gap: 10px;
}
.fotList{
    list-style: none;
    text-decoration: none;
    color: white;
    padding-left: 10px;
    width: 80px;
}
.fotSecc{
    color: #FFF;
    font-family: Patua One;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    text-shadow: 2px 2px 20px #233AA1;
}
.containerInfo{
    display: flex;
    flex-direction: column;
    width: 300px;
}
.titleFotEli{
    color: #FFF;
    text-align: center;
    font-family: Passion One;
    font-size: 20px;
    font-style: normal;
    font-weight: 100;
    margin: 0;
    line-height: normal;
    text-shadow: 2px 2px 20px #233AA1;
}
.textFotEli{
    color: #FFF;
    text-align: center;
    font-family: Patua One;
    font-size: 10px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin: 0;
    text-shadow: 2px 2px 20px #233AA1;
}
.contButtonFooter{
    display: flex;
    flex-direction: row;
    gap: 5px;
    justify-content: center;
    margin-top: 5px;
}
.buttonFooter{
    height: 22px;
    width: 22px;
    background: white;
    border: none;
    border-radius: 5px;
    box-shadow: 2px 2px 20px #233AA1;
    display: flex;
    justify-content: center;
    align-items: center;
}
.iconFoot{
    width: 12px;
    height: 12px;
}
}