.Form{
    width: 800px;
    margin: auto;
    height: 350px;
    background: #05134e33;
    display: flex;
    border-radius: 10px;
}
.containerForm{
    display:  flex;
    flex-direction: column;
    gap: 20px;
    align-items: center;
    justify-content: center;
    margin: auto;
}
.inputForm{
    width: 550px;
    height: 50px;
    border: none;
    border-radius: 10px;

}
.buttonFormEnviar{
    width: 180px;
    height: 40px;
    flex-shrink: 0;
    border-radius: 10px;
    background-color: #071755;
    transition: background-color 0.5s;
    color: white;
    font-size: 18px;
    font-family: 'Paytone One', sans-serif;
    border: none;
    cursor: pointer;
    margin: auto;
}
.buttonFormEnviar:hover{
    background-color: white;
    color: #071755;
}