@import url('https://fonts.googleapis.com/css2?family=Passion+One&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Patua+One&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Pavanam&display=swap');

.contImgCarr{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-top: 80px;
}
.imgArrowID{
    width: 120px;
    height: 90px;
    transition: transform 0.5s;
}
.imgArrowID:hover{
    transform: scale(1.2);
    cursor: pointer;
}
.imgID{
    width: 1200px;
    height: 600px;
}
.containerTitleID{
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 20px 260px;
    width: 1000px;
    padding: 20px;
}
.priceID{
    color: #1a2141;
    font-family: 'Paytone One', sans-serif;
    font-size: 34px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin: 5px 0px;
}
.titleID{
    color: #0a1958;
    font-family: 'Paytone One', sans-serif;
    font-size: 38px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin: 0;
}
.rec{
    width: 200px !important;
}
.contItemsID{
    display: flex;
    flex-direction: row;
    margin: 20px auto;
    gap: 15px;
}
.itemsCardID{
    display: flex;
    flex-direction: row;
    gap: 7px;
}
.imgCaracCardID{
    width: 30px;
    height: 30px;
}
.caracCardID{
    color: #000;
    font-family: Pavanam;
    font-size: 28px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin: 0;
}
.containerUbiText{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    margin-top: 60px;
}
.containerUbi{
    display: flex;
    flex-direction: column;
    width: 400px;
}
.ubiText{
    color: #000;
    font-family: Pavanam;
    font-size: 25px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin: 10px 0px;
    text-align: center;
}
.containerCaract{
    display: flex;
    flex-direction: column;
    margin: 0;
    width: 400px;

}
.listCaract{
    list-style: none;
    padding-left: 0;
    margin: 0;
}
.titleListCaract{
    color: #1a2141;
    font-family: 'Paytone One', sans-serif;
    font-size: 25px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin: 5px 0px;
    text-align: center;
}
.titleForm{
    color: #1a2141;
    font-family: 'Paytone One', sans-serif;
    font-size: 30px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin: 40px 0px 25px 0px;
    text-align: center;
}
.textListCaract{
    color: #000;
    font-family: Pavanam;
    font-size: 25px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin: 5px 0px;
}
.ubicacionID{
    width: 600px;
    height: 230px;
    border-radius: 5px;
}
.frameUbi{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.buttonVer{
    display: flex;
    background: none;
    border: none;
    color: #1C2138;
    font-family: Patua One;
    font-size: 30px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin: 20px auto;
}
.arrowCarac{
    width: 30px;
    height: 40px;
    margin: 0px 5px;
}
.titleCarr{
    color: #1C2138;
    font-family: 'Paytone One', sans-serif;
    font-size: 30px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin: 0;
    text-align: center;
    margin: 60px 0 40px 0;
}
.titleCaracDeta{
    color: #1C2138;
    font-family: 'Paytone One', sans-serif;
    font-size: 30px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin: 0;
    text-align: center;
    margin: 20px 0 20px 0;
}
.textButtonID{
    color: #ffffff;
    font-family: Patua One;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin: 0;
}

/*PHONE*/
@media (min-width: 300px) and (max-width: 500px) {
.contImgCarr{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-top: 80px;
}
.imgArrowID{
    width: 20px;
    height: 80px;
    transition: transform 0.5s;
}
.imgArrowID:hover{
    transform: scale(1.2);
    cursor: pointer;
}
.imgID{
    width: 100%;
    height: 200px;
}
.containerTitleID{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 0px;
    width: 85%;
    padding-bottom: 10px;
}
.priceID{
    color: #1C2138;
    font-family: Patua One;
    font-size: 22px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin: 0;
}
.titleID{
    color: #1C2138;
    font-family: Patua One;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin: 0;
}
.containerButton{
    display: flex;
    flex-direction: row;
    width: 100px;
    justify-content: end;
    gap: 5px;
}
.buttonID{
    width: 20px;
    height: 20px;
    border: none;
    border-radius: 5px;
    background: #233AA1;
    display: flex;
    justify-content: center;
    align-items: center;
}
.rec{
    width: 80px !important;
}
.iconButtonID{
    width: 12px;
    height: 12px;
}
.contItemsID{
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 0px 30px;
    width: 150px;
    gap: 5px;
}
.itemsCardID{
    display: flex;
    flex-direction: row;
    gap: 4px;
}
.imgCaracCardID{
    width: 15px;
    height: 15px;
}
.caracCardID{
    color: #000;
    font-family: Pavanam;
    font-size: 13px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin: 0;
}
.containerUbiText{
    display: flex;
    flex-direction: column;
    margin: 0px 30px;
    width: 80%;
}
.containerUbi{
    display: flex;
    flex-direction: row;
    width: 100%;
    margin: auto;
}
.ubiText{
    color: #000;
    font-family: Pavanam;
    font-size: 13px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin: 20px 0px;
    text-align: start;
}
.containerCaract{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 300px;
    height: 80px;
    gap: 20px;
    margin: 0;
}
.listCaract{
    list-style: none;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 200px;
    height: 30px;
    padding-left: 0;
    margin: 0;
}
.textListCaract{
    color: #000;
    font-family: Pavanam;
    font-size: 13px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin: 2px 0px;
    width: 100px;
}
.ubicacionID{
    width: 100%;
    height: 180px;
    border-radius: 5px;
}
.frameUbi{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 15px auto 15px auto;
}
.buttonVer{
    display: flex;
    align-items: center;
    background: none;
    border: none;
    color: #1C2138;
    font-family: Patua One;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin: 15px auto;
    height: 20px;
}
.arrowCarac{
    width: 20px;
    height: 25px;
    margin: 0px 5px;
}
.titleCarr{
    color: #1C2138;
    font-family: Patua One;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin: 0;
    text-align: center;
    margin: 20px 0 20px 0;
}
.textButtonID{
    color: #ffffff;
    font-family: Patua One;
    font-size: 10px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin: 0;
}
}