.containerCarr{
    display: flex;
    flex-direction: row;
    justify-content: center;
}
.buttonCarr{
    background: none;
    border: none;
    margin-top: 80px;
}
.Carr{
    display: flex;
    flex-direction: row;
    width: 90%;
    overflow: hidden;
    justify-content: center;
    gap: 15px;
}
.buttonImgCarrCard{
    width: 20px;
}

/*PHONE*/
@media (min-width: 300px) and (max-width: 500px) {
.containerCarr{
    display: flex;
    flex-direction: row;
    justify-content: center;
}
.buttonCarr{
    background: none;
    border: none;
}
.Carr{
    overflow-x: auto;
    touch-action: pan-y pan-x;
    display: flex;
    flex-direction: row;
    width: 70%;
    justify-content: center;
    gap: 20px;
}
.buttonImgCarrCard{
    width: 10px;
}
}