.contCarc{
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
}
.Carac{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 0 auto 0 auto;
    justify-content: center;
}
.containerCarac{
    display: flex;
    flex-direction: row;
    
}
.containerTitle{
    background-color: #A1A497;
    border: 1px solid #00000083;
    width: 220px;
    height: 60px;
}
.containerRes{
    background-color: #F3F3F3;
    border: 1px solid #00000083;
    width: 220px;
    height: 60px;
}
.titleCarac{
    color: #1C2138;
    text-align: center;
    font-family: Patua One;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}
.resCarac{
    color: #1C2138;
    text-align: center;
    font-family: Patua One;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}
.containertextLoadCarac{
    display: flex;
    flex-direction: row;
}
.iconLoadCarac{
    width: 28px;
    margin: 0 10px;
}
.containerButtonCarac{
    display: flex;
    justify-content: center;
}
.textLoadCarac{
    color: #1C2138;
    text-align: center;
    font-family: Patua One;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}
.buttonLoadCarac{
    background: none;
    border: none;
}

/*PHONE*/
@media (min-width: 300px) and (max-width: 500px) {
.contCarc{
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
}
.Carac{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 90%;
    height: 430px;
    margin: auto;
    justify-content: center;
}
.containerCarac{
    display: flex;
    flex-direction: row;
}
.containerTitle{
    background-color: #A1A497;
    border: 1px solid #00000083;
    width: 130px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.containerRes{
    background-color: #F3F3F3;
    border: 1px solid #00000083;
    width: 130px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.titleCarac{
    color: #1C2138;
    text-align: center;
    font-family: Patua One;
    font-size: 13px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin: 0;
}
.resCarac{
    color: #1C2138;
    text-align: center;
    font-family: Patua One;
    font-size: 13px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}
}