.backgroundStart{
    background-image: url('../../img/backgroundStart.png');
    background-size: 100%;
    background-position: center;
    height: 600px;
    opacity: 0.5;
    position: absolute; 
    top: 20;
    left: 0;
    width: 100%;
    height: 75%;
    z-index: -1;
}
.containerImgStart{
    padding-top: 100px;
}
.imgStart{
    width: 550px;
    height: 550px;
    border-radius: 100%;
}
.containerInfoStart{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 100px;
    margin-bottom: 100px;
}
.titleEslogan{
    color: #0a1958;
    font-family: 'Paytone One', sans-serif;
    font-size: 60px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin: 0;
    text-align: center;
    width: 700px;
}
.textEslogan{
    color: #06113d;
    font-family: Patua One;
    font-size: 30px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin: 0;
    text-align: center;
    width: 700px;
}
.contButtonStart{
    display: flex;
    flex-direction: row;
    gap: 10px;
    margin: 20px auto 100px auto;
    width: 350px;
}
.buttonStart{
    border: none;
    width: 55px;
    height: 55px;
    border-radius: 10px;
    background-color: #253994;
}
.extendButtonStart{
    width: 200px;
}
.textButtonStart{
    color: #ffffff;
    font-family: Patua One;
    font-size: 21px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin: 0;
}

/*PHONE*/
@media (min-width: 300px) and (max-width: 500px) {
.backgroundStart{
    background-image: url('../../img/backgroundStart.png');
    background-size: 100% 100%;
    background-position: center;
    background-repeat: no-repeat;
    height: 200px;
    opacity: 0.4;
    position: absolute; /* o position: absolute */
    top: 20;
    left: 0;
    width: 100%;
    z-index: -1;
    margin-top: 50px;
}
.containerImgStart{
    padding-top: 140px;
    padding-left: 20px;
}
.imgStart{
    width: 130px;
    height: 130px;
    border-radius: 100%;
}
.containerInfoStart{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 20px;
}
.titleEslogan{
    color: #1933a5;
    font-family: Patua One;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin: 35px 0px 0px 0px;
    text-align: start;
    width: 180px;
    text-shadow: 0px 0px 20px rgb(255, 255, 255);
}
.textEslogan{
    color: #1C2138;
    font-family: Patua One;
    font-size: 15px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin: 0;
    text-align: start;
    width: 200px;
}
.contButtonStart{
    display: flex;
    flex-direction: row;
    gap: 3px;
    margin-top: 5px;
    margin-bottom: 0px;
}
.buttonStart{
    border: none;
    width: 20px;
    height: 20px;
    border-radius: 5px;
    background-color: #233AA1;
    display: flex;
    justify-content: center;
    align-items: center;
}
.extendButtonStart{
    width: 80px;
}
.textButtonStart{
    color: #ffffff;
    font-family: Patua One;
    font-size: 11px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin: 0;
}
.iconButtonStart{
    width: 13px;
    height: 13px;
}
}